*{
    font-family: Helvetica, Arial, sans-serif;
}

body{
    padding: 60px;
}

input{
    font-size: 1.2rem;
    width: 100%;
    color: gray;
}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    background-size: cover;
}

.card p{
    padding: 5px;
    text-align: center;
}

.card img{
    min-height: 170px;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}